import React from 'react';
import PropTypes from 'prop-types';
import ApoVideo from '../images/apotheosis.mp4';

const Video = ({ videoSrcURL, videoTitle, ...props }) => {
  if (videoTitle === 'Apotheosis') {
    return (
      // super hacky workaround for http hosted video
      <div className="video" style={{marginLeft: "60px"}}>
        <video width="800" height="450" controls>
          <source src={ApoVideo} type="video/mp4" />
        </video>
      </div>
    );
  }
  return (
    <div className="video">
      <iframe
        src={videoSrcURL}
        title={videoTitle}
        webkitallowfullscreen="true"
        mozallowfullscreen="true"
        allowFullScreen
        autoPlay="0"
      />
    </div>
  );
};

Video.propTypes = {
  videoSrcURL: PropTypes.string.isRequired,
  videoTitle: PropTypes.string.isRequired,
};
export default Video;
