import React, { Component } from 'react'
import {Modal, Button} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';

const personalText = "My work uses simulation to examine the boundaries of change within seemingly metastable systems. In doing this, I am trying to better understand how political movements grapple with the challenges and paradoxes that come with actualizing social change. \n\nSimulation is the umbrella term that I use for my work, but it is divided up into installation and real-time generative video. The former serves to immerse and implicate the audience in the representations within the latter. The latter is developed with a set of formal constraints that I call indeterminate cinema (IC).\n\nIC can be thought of as automated narrative video art. The central feature of IC is a capacity for infinite change. This change must always have qualities that are outside of my control. A work of indeterminate cinema constructs a pseudo narrative in real-time, with all elements generated with machine learning. The architecture of an IC project is a network of feedback loops. A piece of dialogue is generated and then analyzed for sentiment which triggers a sound-effect-a jump-cut-an-animation which in turn generates a piece of dialogue. Often, viewing this work can feel like watching a system-forward sitcom.\n\nTo the degree that I am able to author the arc of these pseudo-narratives, I take a research-based approach. I have a long history of organizing within Leftist political movements. These groups and the patterns that they replicate across time are my primary focus. Some work examines these patterns through a fuzzy lens, such as the manifesto-trained networks that re-enact the curse of the left in in or, a round sedition, while some work takes a more traditional (faux-prophetic) approach to simulation, such as the data-hungry nets that endlessly reconfigure power relations in f-scale.\n\nMy work is fundamentally concerned with the recognition of social change. Through softwares inexhaustible capacity for iteration and permutation, I am able to make work that samples and sketches the edges of state (machines of state). This is done to amplify sensitivity to state shifts, to identify the presence of movement.";
const collabText = "I have worked extensively as a designer and engineer. I have occupied these roles in artist assistant positions, in creative director roles at commercial studios, and in volunteer roles in non-profits.\n\nOften this work is collaborative in nature. I think of this activity as a way of expanding on my technical skills, but also as a way of building community and exploring ideas that arent naturally present in my studio practice. I enjoy exploring utility and constraint with these projects.";
const teachingText = "Time management is often the challenge that I spend the most thought and energy contending with. As an art instructor working within a highly technical field, there are many needs to juggle, from technical instruction to critique to one-on-one mentorship. Each one of these areas requires generosity and intention from both the teacher and the students. I have two core strategies in managing these goals within the limited available time in class. I encourage students to develop a personal approach self-guided learning so that they can work effectively outside of the class, and I consistently work to weave together all of the distinct elements of instruction so they may support and strengthen one another.\n\nI didnt go to school in the traditional sense during grade school. My parents left me home alone with textbooks and a directive to homeschool myself. This produced mixed results, but during the process, I did learn a lot about how to teach myself. During college, this became a tremendous asset. I was often compelled to develop projects outside the feasible scope of instruction. There wasnt enough time in the classroom to learn everything I needed to learn to make the projects I was interested in. I was able to accomplish these projects because of the affordances that online communities and open-source software provided. This experience has been valuable in helping me to understand how to build a project-based pedagogy that encourages every student to think of themselves as a self-learner.\n\nWhat does it mean to teach students with an emphasis on self-guided learning? For me, it means exposing my process as a learner. When leading tutorials or discussions, I will intentionally show myself getting confused or lacking the necessary information. I do this with no indication of shame, instead I go through the motions of looking up the information or troubleshooting the mistake. I ask students to help me through this process. I do this often. I make sure to consistently drill home that expert-status is fallible and that even the instructor needs to have a toolset for finding answers to difficult questions. In addition to these demonstrations, I provide students with extensive resources in an easily accessible central repository such as a class website. I then use these resources in class, showing students how I refer to them. That way I have a vocabulary of familiar actions to offer students when they come to me with questions.\n\nI orient my classroom around self-guided learning, but I fully understand that many students do not enter the classroom with prior experience in this approach. Also, every student has a different learning style. I take this into account when developing my syllabus by creating a clear, consistent, and thoroughly documented structure for instruction. I find that all students respond well to repetitive and predictable structures. Through repeated and expected exposure to critique, tutorial, discussion, and lecture, students learn to understand each module as a different learning state that requires a different type of engagement. Not every student will love every module. This is an inevitable fact, but this repetition allows me to balance the engagement in the class. Perhaps a student enjoys lecture but dislikes tutorial, or likes one-on-one meetings but not discussion. By keeping every module concise and predictable, students can anticipate when the segments they relate to most will come again. This establishes a sense of momentum, even when a student feels disinterested in a current module, they have a future module to look forward to.\n\nWhenever possible I try to apply a formula to each class, and model every session after a template. This template can have several configurations, but it is essential that a student enters class knowing which version of the template they will experience that day. A discussion and studio class may open with a discussion of a reading, followed by in-class studio time with rotating one-on-one check-ins. A tutorial and milestone class may open with a short presentation of relevant work from the field, followed by a technical tutorial based on the work presented, and ending with confirmation of progress on self-assigned milestones. These templates are flexible and class-dependent, but the important thing is that there are not too many variants in any one class and that each student easily understands what to expect from a particular variant. \n\nWhile the modules that make up my class may appear discrete, I work very hard to make sure that each learning state is in dialog with all others. During a technical tutorial, I might critique the GUI using ideas from Black Gooey Universe by American Artist. During a discussion of embodiment and a screening of Marc Owens Avatar Machine, I may add a technical overview of composition and viewer identification in game development. I am always looking for new ways that theory and praxis can inform one another.\n\nI evaluate my success based on engagement and comfort. Do my students seem comfortable expressing uncertainty? Has the classroom become a space in which we can discuss difficult ideas without intimidation? Are students able to give clear, honest, and kind feedback to one another? I also assess my performance by asking myself whether the students are attempting to establish connections between disparate ideas. As a professor, I love it when I can see that a student feels empowered and excited by newfound knowledge or ability. Technical instruction often provides this. But for me, it is even more rewarding when a student summons the courage to subvert foregone conclusions about the appropriate applications of that knowledge.";

function MyVerticallyCenteredModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        style={{color: "var(--main-theme-color)"}}
      >
        <Modal.Header closeButton>
        </Modal.Header>
        <Modal.Body style={{
            color: "var(--main-theme-color)",
            maxHeight: "70vh",
            overflowY: "scroll",
            }}>
          <p style={{maxWidth: "80%", marginLeft : "10%", textAlign : "justify", whiteSpace: "pre-wrap"}}>
            {getText(props.workType)}
          </p>
        </Modal.Body>
        <Modal.Footer>
          <Button style={{backgroundColor: 'var(--main-theme-color)'}} onClick={props.onHide}>Close</Button>
        </Modal.Footer>
      </Modal>
    );
  }

function getText(workType) {
  switch(workType) {
    case 'personal':
      return personalText;
    case 'collab':
      return collabText;
    case 'student':
      return teachingText;
    default:
      return "No statement for this page.";
  }
}

class Writing extends Component {
  constructor(props) {
    super(props)
    this.workType = props.workType;
    this.state = {
      isModalOpen: false,
    }
  }
  handleModalOpen = event => {
    console.log('handleModalOpen: ', event);
    this.setState({ isModalOpen: true })
  }

  handleModalClose = event => {
    console.log('handleModalOpen: ', event);
    this.setState({ isModalOpen: false })
  }

  render() {
    // if(this.workType === 'collab') {
    //   return null
    // }
    return (
        <>

          <Button id="writing-link" variant="outline-light" onClick={this.handleModalOpen}>
            statement
          </Button>
        <MyVerticallyCenteredModal
        show={this.state.isModalOpen}
        onHide={this.handleModalClose}
        workType={this.workType}
      />
        </>
    )
  }
}

export default Writing